#Viewer {
    position: relative;
    width: 640px;
    height: 390px;
  }
  
  #Viewer.fullscreen {
    width: 100%;
    height: 100%;
  }
  
  #FullScreen {
    cursor: pointer;
    position: absolute;
    bottom: 10px;
    right: 10px;
    padding: 5px;
    background: #38f;
    color: #fff;
  }